let that;
import {
	searchGoodsClass,
	setSts
} from '@/axios/index.js'
export default {
	data() {
		return {
			seo: '',
			seos: '',
			activityList: [

			],
			pageInfo: {
				page_cnt: 0,
				which_page: 1,
				page_size: 4,
				page_start: 1,
				page_end: 0,
				total_cnt: 0
			}
		}
	},
	onLoad(obj) {
		that = this;
		that.seo = obj.seo;
		that.seos = obj.seo;
	},
	mounted() {
		uni.showLoading({
			title: '加载中.....'
		})
		that.activityList.splice(0, that.activityList.length);
		that.init(1, 100); //初始化查询数据
	},
	methods: {
		queryActivity() {
			that.activityList.splice(0, that.activityList.length);
			that.seo = that.seos ? that.seos : '';
			that.init(1, 100);
		},
		init: function(which_page, pageSize) { //初始化查询
			searchGoodsClass({
				seo: that.seo,
				getDetail: 1,
				type: 2,
				which_page: which_page,
				pageSize: pageSize
			}).then(res => {
				if (res.ret == -1) {
					uni.showToast({
						title: '查询失败',
						complete: () => {
							uni.hideToast();
						}
					});
				} else if (res.ret == 0) {
					that.pageInfo = res.info.pageInfo;
					let count = 0;
					console.log(res.info.list)
					res.info.list.forEach(item => {
						if (that.activityList.length > 0) {
							that.activityList.forEach(i => {
								if (i.id == item.id) {
									count++;
								}
							})
							if (count == 0) {
								that.activityList.push(item);
							}
						} else {
							that.activityList.push(item);
						}
					});
				}
				uni.hideLoading();
			});
		}
	}
}
